<template>
    <form class="row" @submit.prevent="submit()" >
        
        <div class="col-lg-12">
            <div class="card">
                
                <div class="card-body" >
                    <div class="row">
                        <div class="col-md-12">
                            <h3>Cadastro de Parâmetros do Sistema <span v-if="form.id">: {{form.nome}}</span> </h3>
                        </div>
                    </div>
                    <hr>
                    <div class="row">                        
                        <div class="col-md-12">
                            
                            <div class="row">
                                <div class="col-md-12">
                                    <h4>Dados Cadastrais</h4>
                                </div>
                            </div>
                            <hr>
                            <div class="row">
                                <label class="col-sm-3 col-form-label" v-show="form.id">Id</label>
                                <div class="col-sm-9" v-show="form.id">
                                    <input-componente
                                        :disabled="true"
                                        v-model="form.id"                            
                                        />
                                </div>

                                <label class="col-sm-3 col-form-label">Nome</label>
                                <div class="col-sm-9">
                                    <input-componente
                                        v-model="form.nome"
                                        :validator="$v.form.nome"
                                        @blur="$v.form.nome.$touch"
                                        />
                                </div>

                                <label class="col-sm-3 col-form-label">Valor</label>
                                <div class="col-sm-9">
                                    <input-componente
                                        v-model="form.valor"
                                        :validator="$v.form.valor"
                                        @blur="$v.form.valor.$touch"
                                        />
                                </div>


                                <label class="col-sm-3 col-form-label">Descrição</label>
                                <div class="col-sm-9">
                                    <input-componente
                                        v-model="form.descricao"
                                        :validator="$v.form.descricao"
                                        @blur="$v.form.descricao.$touch"
                                        />
                                </div>

                            </div>     
                        </div>
                    </div>
                </div>
                <div class="row">
                    <div class="col-md-12 text-center" v-if="erros || sucesso">
                        <mensagem-componente :titulo="errosTitulo" v-if="erros" :erros="erros" @fechar="erros = null"></mensagem-componente>
                        <mensagem-componente v-if="sucesso" :sucesso="sucesso" @fechar="sucesso = null"></mensagem-componente>
                    </div>
                    <div class="col-md-12 button-items text-center">

                        <router-link class="btn btn-warning waves-effect waves-light w-sm" title="Voltar" :to="{name: 'parametrosSistemaLista'}">
                            <i class="bx bx-arrow-back d-block font-size-16"></i> Voltar
                        </router-link>

                        <button type="submit" class="btn btn-primary waves-effect waves-light w-sm" title="salvar">
                            <i class="bx bxs-save d-block font-size-16"></i> Salvar
                        </button>

                    </div>
                </div>
                <div class="row">
                    <div class="col-md-12 text-right" v-if="form.id">
                        <log-componente :index="form.id" subject_type="App\Models\Cliente\Parametro"  :subject_id="form.id" ></log-componente>
                    </div>
                </div>
            </div>
        </div>        
    </form>
</template>

<script>

import { required,  maxLength, } from "vuelidate/lib/validators";
import { ParametroSistemaService } from '../../../services'



const parametrosSistemaService = new ParametroSistemaService();

export default {
  data() {
        return {
            form: {
                id: null,
                nome: '',
                descricao: '',
                valor: ''

            },
          
            sucesso: null,
            erros:null,
            errosTitulo:'',

        } ;
    },
    validations() {
        return {
            form: {
                nome: { required, maxLength:maxLength(100)},
                valor: { required, maxLength:maxLength(200)},
                descricao: { maxLength:maxLength(200)},                
            },
        }
    },
    mounted() {
        if (this.$route.params.id) {
            this.buscarObjeto(this.$route.params.id);
        }       
    },
    methods: {

        submit: function(){
            this.$v.form.$touch();
            this.erros = null;
            this.sucesso = null;    
            this.errosTitulo ='';

            if (this.$v.form.$invalid){
                this.errosTitulo = 'Preencha o formulário corretamente, alguns campos estão com erros!'
                this.erros = this.$v.form;
                return false;
            }
            let obj = {...this.form};
            
            let request = null;
            if(obj.id)    
                request = parametrosSistemaService.atualizar(obj);
            else
                request = parametrosSistemaService.criar(obj);

            request.then(
                response => {
                    let redireciona = !this.form.id;
                    this.form = response.data;
                    this.sucesso = "Registro salvo com sucesso!";
                    if(redireciona) {
                        let index = String(this.$route.path).lastIndexOf("/");
                        let newPath = String(this.$route.path).slice(0, index);
                        newPath += "/" + this.form.id;
                        this.$router.push(newPath);
                        this.buscarObjeto(this.form.id);
                    }
                }   
            ).catch( erro => this.erros = erro )

        },

        buscarObjeto: function(id){
            parametrosSistemaService.buscar(id)
                .then(  response => {
                    this.form = response.data;
                }).catch( erro => this.erros = erro)
        },

        buscarCentros: function(){
            parametrosSistemaService.listar({ordenar: 'codigo', ordem: 'asc', per_page:100})
                .then(  response => {
                    this.centros = response.data.data;
                }).catch( erro => this.erros = erro)
        },
    }
};
</script>
